<template>
  <div>
    <el-breadcrumb separator="/" style="margin-bottom: 50px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/prize">中奖列表</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form :inline="true" @submit.native.prevent="onSubmit" :model="form" class="form-search">
      <el-row>
        <el-col>
          <el-form-item label="手机号">
            <el-input size="small" placeholder="请输入内容" v-model="form.mobile"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="姓名">
            <el-input size="small" placeholder="请输入内容" v-model="form.name"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="奖品名">
            <el-input size="small" placeholder="请输入内容" v-model="form.jp_name"></el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="是否在获奖列表显示">
            <el-select size="small" v-model="form.is_show_index" clearable placeholder="请选择">
              <el-option value="Y" label="是" />
              <el-option value="N" label="否" />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="状态">
            <el-select size="small" v-model="form.status" clearable placeholder="请选择">
              <el-option
                v-for="(value, key) in config.status"
                :value="key"
                :label="value"
                :key="value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col>
          <el-button native-type="submit" type="primary">提交</el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      form: {
        status: 'Y',
        is_show_index: 'Y',
      },
      config: {},
      file: [],
    }
  },
  mounted() {
    const id = this.$router.history.current.params.id
    var url = '/user/prize/config'
    if (id) {
      url = '/user/prize/view'
    }

    Fetch(url, { id: id }).then(r => {
      if (r.data.view) {
        this.form = r.data.view
        this.config = r.data.config
        this.file = [
          {
            url: r.data.view.thumb,
            name: r.data.view.thumb,
          },
        ]
      } else {
        this.config = r.data
      }
    })
  },
  methods: {
    ssid() {
      return localStorage.getItem('ssid')
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      var url = '/user/prize/create'
      if (id) {
        url = '/user/prize/update'
      }
      Fetch(url, {
        id: this.$router.history.current.params.id,
        ...this.form,
      }).then(() => {
        Message.success('操作成功')
        this.$router.replace('/prize')
      })
    },
    fileSuccess(res) {
      this.form.thumb = res.data.file
    },
  },
}
</script>
<style lang="less" scoped>
.form-search {
  max-width: 800px;

  /deep/ .el-input {
    width: 300px;
  }

  /deep/ .el-form-item__label {
    width: 140px;
    text-align: right;
  }
}
</style>
